export function getSchedulingField(form) {
  console.log(form)
  let params = {}
  if(form){
    if (form.SubmitTime) params.SubmitTime = form.SubmitTime
    if (form.OperatorID) params.OperatorID = form.OperatorID
    if (form.AisleCode) params.AisleCode = form.AisleCode
    if (form.Area) params.Area = form.Area.join(',')
    if (form.Attribute) params.Attribute = form.Attribute
    if (form.StartTime) params.StartTime = form.StartTime
    if (form.StopTime) params.StopTime = form.StopTime
    if (form.DispatcherID) params.DispatcherID = form.DispatcherID
  }
  return params
}

